import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { SpiComponent } from './spi/spi.component';
import { ErrorpageComponent } from './errorpage/errorpage.component';
import { SampleComponent } from './sample/sample.component';
import { DummyComponent } from './dummy/dummy.component';
import { PaginationComponent } from './pagination/pagination.component';
import { PipesComponent } from './pipes/pipes.component';
import { EmpinfoComponent } from './empinfo/empinfo.component';
import { AuthGuard } from './auth.guard';
import { EmplistComponent } from './emplist/emplist.component';
import { ChartsComponent } from './charts/charts.component';
import { PiechartComponent } from './piechart/piechart.component';


const routes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: '', component: SignupComponent },
  { path: 'signup', component: SignupComponent },
  { path: 'spi', component: SpiComponent, canActivate: [AuthGuard] },
  { path: 'sample', component: SampleComponent, canActivate: [AuthGuard] },
  { path: 'dummy', component: DummyComponent, canActivate: [AuthGuard] },
  { path: 'pagination', component: PaginationComponent, canActivate: [AuthGuard] },
  { path: 'pipes', component: PipesComponent, canActivate: [AuthGuard] },
  { path: 'emplist', component: EmplistComponent, canActivate: [AuthGuard] },
  { path: 'empinfo', component: EmpinfoComponent, canActivate: [AuthGuard] },
  { path: 'charts', component: ChartsComponent, canActivate: [AuthGuard] },
  { path: 'piechart', component: PiechartComponent, canActivate: [AuthGuard] },


  { path: '**', component: ErrorpageComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
