export default "body {\n  margin: 0;\n  padding: 0;\n  font-family: sans-serif;\n}\n\n.box {\n  position: absolute;\n  top: 55%;\n  left: 50%;\n  -webkit-transform: translate(-50%, -50%);\n          transform: translate(-50%, -50%);\n  width: 600px;\n  box-sizing: border-box;\n  border-radius: 10px;\n  background: rgba(230, 230, 238, 0.055);\n  border: solid 1px rgba(255, 166, 0, 0.281);\n}\n\n.box:hover {\n  background: rgba(230, 230, 238, 0.137);\n  box-shadow: 5px 10px 10px rgba(241, 205, 4, 0.295);\n  transition: -1s;\n  border: transparent;\n}\n\n.box h1 {\n  padding: 0;\n  color: whitesmoke;\n  text-align: center;\n  color: #030303;\n  font-weight: bold;\n  text-shadow: 3px 3px 3px rgba(206, 141, 20, 0.904);\n  /* text-decoration: underline; */\n}\n\n.box .inputBox {\n  position: relative;\n  margin: 40px;\n}\n\n.box .inputBox input {\n  width: 100%;\n  padding: 5px;\n  font-size: 20px;\n  color: #ebe3d4;\n  margin-bottom: 10px;\n  border: none;\n  border-bottom: 2px solid black;\n  outline: none;\n  background: transparent;\n  /* background: transparent; */\n}\n\n.box .inputBox input:hover ~ label, .box .inputBox input:valid ~ label {\n  top: -30px;\n  left: 0;\n  color: #e7be35;\n  font-size: 25px;\n}\n\n.box .inputBox label {\n  position: absolute;\n  top: 0;\n  left: 0;\n  padding: -20px;\n  font-size: 30px;\n  color: #e7be35;\n  pointer-events: none;\n  transition: -5s;\n}\n\n.box input[type=button] {\n  border: none;\n  outline: none;\n  color: whitesmoke;\n  font-size: 25px;\n  text-align: center;\n  background: rgba(206, 141, 20, 0.904);\n  height: 40px;\n  width: 200px;\n  margin-left: 200px;\n  margin-bottom: 30px;\n  cursor: pointer;\n  border-radius: 20px;\n}\n\n.box input[type=button]:hover {\n  background: rgba(23, 100, 17, 0.911);\n  box-shadow: 5px 10px 10px rgba(3, 3, 3, 0.712);\n  transition: -1s;\n  color: whitesmoke;\n}\n\n.box input[type=button]:disabled {\n  background: gray;\n}\n/*# sourceMappingURL=data:application/json;base64, */"