import { hostViewClassName } from '@angular/compiler';
import { Directive, ElementRef, HostListener } from '@angular/core';

@Directive({
  selector: '[appHighlight]'
})
export class HighlightDirective {

  constructor(private Lref: ElementRef) { 
    console.log('highlight diretive');
    console.log(this.Lref);
    console.log(this.Lref.nativeElement);
    this.Lref.nativeElement.style.backgroundColor='red';
    this.Lref.nativeElement.style.fontSize='40px';    
  }

  @HostListener('click')
  onclick(){
    this.Lref.nativeElement.style.backgroundColor='orange';
    this.Lref.nativeElement.style.fontSize='30px'; 
  }
  @HostListener('mouseover')
  onmouseover(){
    this.Lref.nativeElement.style.backgroundColor='Blue';
    this.Lref.nativeElement.style.fontSize='25px'; 
  }
  @HostListener('mouseleave')
  onmouseleave(){
    this.Lref.nativeElement.style.textDecoration='none';
    this.Lref.nativeElement.style.fontSize='15px'; 
  }
}
