import { Component, OnInit, OnDestroy } from "@angular/core";

@Component({
  selector: "app-dummy",
  templateUrl: "./dummy.component.html",
  styleUrls: ["./dummy.component.scss"]
})
export class DummyComponent implements OnInit, OnDestroy {
  showme = true;
  showme2 = false;
  sample = [5, 10, 15, 20, 25];
  inputtype = 'password';

  constructor() { }

  ngOnInit() {
    console.log(this.sample); //For initail load at browser (use console for understand)
  }

  jsclick() {
    // alert('Thanks for clicking me!')
    this.showme = !this.showme;
    this.showme2 = !this.showme2;
  }

  delete(index: number) {
    console.log(index);
    this.sample.splice(index, 1);
  }

  addElement() {
    let newValue = Math.random();
    // let index = Math.floor (Math.random() * 5);
    // this.sample.push(this.sample[index]);
    this.sample.push(newValue);
  }

  ngOnDestroy(){
    console.log('ngOnDestroy');
  }
}
